import { h } from 'preact';
import { Select } from '../components/select';
import { SelectMultiple } from '../components/select-multiple';
import { InputDate } from '../components/date';
import { Button } from '../components/button';
import { flex, flexCol, flexItem20, highlight } from '../constants';
import { SelectMultipleFilter } from '../components/select-multiple-filter';
export function LineGraphControls({ clearCountries, countries, countryKeys, onChange, onUpdateCountryFilter, reload, selectCountry, selectCountries, state, strings, }) {
    const dataSets = [
        strings.series.activeCases,
        strings.series.confirmedCases,
        strings.series.deaths,
        strings.series.recoveries,
        strings.series.estimatedActiveCases,
    ];
    const modes = [
        strings.modes.byDate,
        strings.modes.byFirst,
        strings.modes.byFirst100,
    ];
    const scaleTypes = [
        strings.scaleTypes.linear,
        strings.scaleTypes.logarithmic,
    ];
    const showStates = ['Show States', 'Hide States'];
    const metrics = [strings.metrics.byValue, strings.metrics.byPercent];
    function selectMode(mode) {
        onChange(Object.assign(Object.assign({}, state), { mode: parseInt(mode + '', 10) }));
    }
    function selectDate(ymdString) {
        onChange(Object.assign(Object.assign({}, state), { startDate: ymdString }));
    }
    function selectDataSets(dataSetIndexes) {
        onChange(Object.assign(Object.assign({}, state), { dataSetIndexes: dataSetIndexes.map(d => parseInt(d + '', 10)) }));
    }
    function selectScaleType(scaleType) {
        onChange(Object.assign(Object.assign({}, state), { scaleType: parseInt(scaleType + '', 10) }));
    }
    function selectShowStates(showOrHide) {
        onChange(Object.assign(Object.assign({}, state), { showStates: parseInt(showOrHide + '', 10) === 0 ? false : true }));
    }
    function selectMetric(metric) {
        onChange(Object.assign(Object.assign({}, state), { byMetric: parseInt(metric + '', 10) }));
    }
    return (h("section", { className: `${flex} ${flexItem20}` },
        h("section", { className: flexCol },
            h(Select, { onChange: selectMode, options: modes, selected: state.mode }),
            h(Select, { onChange: selectScaleType, options: scaleTypes, selected: state.scaleType }),
            h(InputDate, { onChange: selectDate, ymdString: state.startDate })),
        h(SelectMultiple, { onChange: selectDataSets, options: dataSets, selected: state.dataSetIndexes }),
        h(SelectMultipleFilter, { classes: countryKeys.length === 0 ? [highlight] : [], filter: state.countryFilter, onUpdateFilter: onUpdateCountryFilter, onChange: selectCountries, onClear: clearCountries, onDeselect: selectCountry, options: countries.filter(filterStates(state.showStates, strings)), selected: countryKeys }),
        h("div", { className: flexCol },
            h(Select, { onChange: selectShowStates, options: showStates, selected: state.showStates ? 1 : 0 }),
            h(Select, { onChange: selectMetric, options: metrics, selected: state.byMetric }),
            h(Button, { label: strings.lineGraph.reload, onClick: reload }))));
}
function filterStates(doFilter, strings) {
    return (item) => {
        if (doFilter === false) {
            return true;
        }
        // special case for Korea
        if (item.name.toLowerCase().indexOf('korea') > -1) {
            return true;
        }
        if (item.name.indexOf(',') > -1) {
            if (item.name.indexOf(`, ${strings.countries.total}`) > -1) {
                return true;
            }
            return false;
        }
        return true;
    };
}
