import { h } from 'preact';
export function AboutList({ title, list, }) {
    return (h("section", null,
        h("h3", null, title),
        h("ul", null, list.map(d => (h("li", null,
            h("strong", null,
                d.name,
                ":"),
            "\u00A0",
            typeof d.description === 'function'
                ? d.description()
                : d.description))))));
}
