import dl from '../i18n/en.json';
import { objReduce, isString, noop } from '@ch1/utility';
export const defaultLanguage = convertJsonToInjectedJson(dl);
export function getLanguage(lang) {
    return import(`../i18n/${lang}.json`).then(s => convertJsonToInjectedJson(s));
}
function convertJsonToInjectedJson(strings) {
    return objReduce(strings, (s, node, key) => {
        if (isString(node)) {
            Object.defineProperty(s, key, {
                get: () => node,
                set: noop,
            });
        }
        else {
            s[key] = convertJsonToInjectedJson(node);
        }
        s[key] = node;
        return s;
    }, {});
}
