import Router, { route } from 'preact-router';
import { Component, h, render as preactRender } from 'preact';
import { LineGraph } from './feature-line-graph/line-graph';
import { createState, loadState, saveState } from './state';
import { selectData, fetchData } from './data';
import { log } from './utility';
import { LearningTable } from './feature-learning-table/learning-table';
import { Header } from './components/header';
import { fullSize, flexCol } from './constants';
import { Geography } from './feature-geography/geography';
import { About } from './feature-about/about';
export class App extends Component {
    constructor(props) {
        super();
        let state = loadState(props.strings);
        if (!state) {
            log(props.strings.app.log.noState);
            state = createState(props.strings);
        }
        this.state = state;
        this.selectAndUpdate();
        this.menu = {
            labels: props.strings.app.menu.map(s => s.name),
            onClick: (selected) => {
                const routePath = this.props.strings.app.menu[selected].route ||
                    this.props.strings.app.menu[0].route;
                this.setState(Object.assign(Object.assign({}, this.state), { routePath }));
                this.menu.selected = selected;
                route(routePath);
                this.selectAndUpdate();
            },
            selected: props.strings.app.menu.reduce((s, n, i) => {
                if (n.route === this.state.routePath) {
                    return i;
                }
                return s;
            }, 0),
        };
    }
    updateSelectState({ countries, series, }) {
        this.setState(Object.assign(Object.assign({}, this.state), { countries, currentSeries: series }));
    }
    selectAndUpdate() {
        this.state.dataPromise
            .then(({ timeSeries }) => {
            this.setState(Object.assign(Object.assign({}, this.state), { data: timeSeries }));
            return selectData(this.props.cache, this.state);
        })
            .then(this.updateSelectState.bind(this))
            .then(() => saveState(this.state));
    }
    lineGraphState(lgs) {
        this.setState(Object.assign(Object.assign({}, this.state), { lineGraphState: lgs }));
        this.selectAndUpdate();
    }
    tableState(tableState) {
        this.setState(Object.assign(Object.assign({}, this.state), { tableState }));
        this.selectAndUpdate();
    }
    reload() {
        this.props.reset();
        this.setState(Object.assign(Object.assign({}, this.state), { dataPromise: fetchData(this.props.strings).then(d => {
                this.selectAndUpdate();
                return d;
            }) }));
    }
    clearCountries() {
        this.setState(Object.assign(Object.assign({}, this.state), { countryKeys: [] }));
        this.selectAndUpdate();
    }
    selectCountry(countryKey) {
        if (this.state.countryKeys.indexOf(countryKey) > -1) {
            this.setState(Object.assign(Object.assign({}, this.state), { countryKeys: this.state.countryKeys.filter(i => i !== countryKey) }));
        }
        else {
            this.setState(Object.assign(Object.assign({}, this.state), { countryKeys: this.state.countryKeys.concat([countryKey]) }));
        }
        this.selectAndUpdate();
    }
    selectCountries(countryKeys) {
        this.setState(Object.assign(Object.assign({}, this.state), { countryKeys }));
        this.selectAndUpdate();
    }
    render() {
        return (h("div", { className: `${fullSize} ${flexCol}` },
            h(Header, { strings: this.props.strings }),
            h(Router, null,
                h(LineGraph, { path: this.props.strings.app.menu[0].route, clearCountries: this.clearCountries.bind(this), countries: this.state.countries, countryKeys: this.state.countryKeys, currentSeries: this.state.currentSeries, menu: this.menu, onChange: this.lineGraphState.bind(this), key: "0", reload: this.reload.bind(this), selectCountry: this.selectCountry.bind(this), selectCountries: this.selectCountries.bind(this), state: this.state.lineGraphState, strings: this.props.strings }),
                h(LearningTable, { countryKeys: this.state.countryKeys, key: "1", onChange: this.tableState.bind(this), menu: this.menu, path: this.props.strings.app.menu[1].route, state: this.state.tableState, selectCountry: this.selectCountry.bind(this), strings: this.props.strings, timeSeries: this.state.data }),
                h(Geography, { key: "2", path: this.props.strings.app.menu[2].route, menu: this.menu, strings: this.props.strings, timeSeries: this.state.data }),
                h(About, { key: "3", path: this.props.strings.app.menu[3].route, strings: this.props.strings, menu: this.menu }))));
    }
}
export function render(root, strings) {
    let cache = {};
    preactRender(h(App, { cache: cache, reset: () => (cache = {}), strings: strings }), root);
}
